import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

export const indexSlice = createApi({
  reducerPath: 'api',
  baseQuery: fetchBaseQuery({
    baseUrl: '/api',
    credentials: 'include',
  }),
  tagTypes: ['auth', 'users'],
  endpoints: () => ({}),
});

export default indexSlice;